import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";


const CorporateActivity = (props) => {
    const [searchValue, setSearchValue] = React.useState('');



    let todayItems = (<>
        <TableRow >
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}Ronaldo</span></TableCell>
            <TableCell className="padding-sides-0 color-white">Permission changes</TableCell>
            <TableCell className="padding-sides-25 color-white">Send invite to<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}robert@lewandowski.com</span> </TableCell>
            <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
        </TableRow>
        <TableRow >
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}Lionel Messi</span></TableCell>
            <TableCell className="padding-sides-0 color-white">Permission changes</TableCell>
            <TableCell className="padding-sides-25 color-white">Action detail here must be long or short <span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}robert@lewandowski.com</span> </TableCell>
            <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
        </TableRow>
        <TableRow >
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}Lionel Messi</span></TableCell>
            <TableCell className="padding-sides-0 color-white">Permission changes</TableCell>
            <TableCell className="padding-sides-25 color-white">Action detail here must be long or short <span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}robert@lewandowski.com</span> </TableCell>
            <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
        </TableRow>
    </>);




    let tbodyData = (<>
        <TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="4">
                <b className="color-white">Today</b>
                <span className="color-grey inline">April 3</span>
            </TableCell>

        </TableRow>
        {props.activityData.map((i, key) => {
            return  ( <TableRow >
                <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{i.user}</span></TableCell>
                <TableCell className="padding-sides-0 color-white">{i.action}</TableCell>
                <TableCell className="padding-sides-25 color-white">{i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span> </TableCell>
                <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
            </TableRow>);
        })}

        <TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="4">
                <b className="color-white">Yesterday</b>
                <span className="color-grey inline">April 2</span>
            </TableCell>
        </TableRow>

        {props.activityData.map((i, key) => {
            return  ( <TableRow >
                <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{i.user}</span></TableCell>
                <TableCell className="padding-sides-0 color-white">{i.action}</TableCell>
                <TableCell className="padding-sides-25 color-white">{i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span> </TableCell>
                <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
            </TableRow>);
        })}
    </>);



    let searchedData = props.activityData.filter(i => {
        if (i.user.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.description[0].toLowerCase().includes(searchValue.toLowerCase())||
            i.description[1].toLowerCase().includes(searchValue.toLowerCase())||
            i.action.toLowerCase().includes(searchValue.toLowerCase())) {
            return i
        }
    })

    searchedData = searchedData.map((i, key) => {

          return  ( <TableRow key={key} >
                    <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{i.user}</span></TableCell>
                    <TableCell className="padding-sides-0 color-white">{i.action}</TableCell>
                    <TableCell className="padding-sides-25 color-white">{i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span> </TableCell>
                    <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
                </TableRow>);
    });



    return (
        <div className="ÑustomStyle">
                <div className="filterPanel">
                    <TextField
                        variant="filled"
                        fullWidth
                        className="search_area"
                        placeholder="Search"
                        value={searchValue}
                        onChange={(e)=>{setSearchValue(e.target.value)}}
                       >
                    </TextField>
                    <div className="search_additional_components">
                        <div className="calendar">{IconPack.searchComponents.calendar}</div>
                        <div>{IconPack.searchComponents.filter}</div>
                    </div>
                </div>
            <Table className="configTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 w160">User</TableCell>
                        <TableCell className="padding-sides-0 w140">Action</TableCell>
                        <TableCell className="padding-sides-25">Description</TableCell>
                        <TableCell className="padding-sides-25 w140 td-date"> {IconPack.tableIcons.date} Date</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {searchValue.length ? searchedData : tbodyData }
                </TableBody>
            </Table>
        </div>
    );
}

const mapStateToProps = (state) => ({
    activityData: state.corporateLicenseActivity.activity,
});


export default connect(mapStateToProps)(CorporateActivity);